@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@100;300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.app{border:1px solid #FEF9EF;background-color:red;display:flex;justify-content:center;max-width:450px;max-height:800px}

*{font-family:'Montserrat Alternates', sans-serif;box-sizing:border-box}

.home{background-color:#ffb48f;color:white;display:flex;flex-direction:column;justify-content:space-around;align-items:center;text-align:center;height:100vh;width:100vw;padding:10px;position:relative;transition:1s}.home a{text-decoration:none}.home.active{transform:translateY(100%)}.logo{margin-bottom:0px;font-weight:100;font-size:3.5rem;margin-top:25px}.home_button{z-index:20;background-color:#f5e6cc;position:relative;color:#41b3a3;border:none;background-color:rgba(255,255,255,0.5);padding:15px 25px;border-radius:20px;font-weight:600;font-size:0.8rem;margin-bottom:15px;transition:0.5s}.grass{position:absolute;background-color:transparent;width:100%;left:0;bottom:0;z-index:2;max-height:100%}.stork{height:55vh;z-index:3;background-color:transparent;position:relative;bottom:18px}

.info-container{display:flex;flex-direction:column;width:100%;top:0;left:0;height:100%;justify-content:center;align-items:center;background-color:#ffb48f;position:fixed;transition:1s;transform:translateY(-100%)}.info-container.active{transform:none}.info-button{z-index:20;position:relative;color:#41b3a3;border:none;background-color:rgba(255,255,255,0.7);padding:15px 25px;border-radius:20px;font-weight:600;font-size:0.8rem;margin-top:35px;transition:0.5s}.info-card{width:90vw;height:80vh;background-color:#17c3b2 !important;color:white !important;display:flex;flex-direction:column;align-items:center;justify-content:center}.info-card h1{margin:0;padding:12px}.info-card a{text-decoration:none}

.registration{height:100vh;width:100vw;overflow:hidden;display:flex;align-items:center;justify-content:center}.registration p{color:#FE6D73;background-color:transparent}.registration p::before{display:inline;content:"⚠ "}.registration button:hover,.registration input:hover{background:#FFCB77}.registration button:active,.registration input:active,.registration input:active{transition:0.3s all;transform:translateY(3px);border:1px solid transparent;opacity:0.8}.registration input:disabled{opacity:0.4}.registration input:hover{transition:0.3s all}.registration-form{width:80%;height:80%;display:flex;flex-direction:column;background-color:#17C3B2;box-shadow:10px 9px 26px -7px #000;border-radius:20px;padding:15px}.registration-form label,.registration-form h1{background-color:inherit;color:#FEF9EF;margin:10px 0px}.registration-button{position:relative;color:#FEF9EF;border:none;background-color:#0081a7;padding:15px 25px;border-radius:20px;font-weight:600;font-size:0.8rem;margin-top:35px;transition:0.5s}.registration-input{background:#FEF9EF;color:#227C9D;border:none;height:3rem;padding:0.5rem;font-size:16px;font-weight:100;letter-spacing:10px;border-radius:5px;font-weight:400;font-size:1rem;letter-spacing:normal}h5{text-align:center;margin-top:2px;font-size:0.9rem;font-weight:700;background-color:transparent;color:#227C9D}h5 a{background-color:inherit;color:inherit;text-decoration:underline}

.login{height:100vh;width:100vw;overflow:hidden;display:flex;align-items:center;justify-content:center}.login p{color:#FE6D73;background-color:transparent}.login p::before{display:inline;content:"⚠ "}.login button:hover,.login input:hover{background:#FFCB77}.login button:active,.login input:active,.login input:active{transition:0.3s all;transform:translateY(3px);border:1px solid transparent;opacity:0.8}.login input:disabled{opacity:0.4}.login input:hover{transition:0.3s all}.login-form{width:80%;height:80%;display:flex;flex-direction:column;background-color:#17C3B2 !important;box-shadow:10px 9px 26px -7px #000;border-radius:20px;padding:15px}.login-form label,.login-form h1{background-color:inherit;color:#FEF9EF;margin:10px 0px}.login-button{position:relative;color:white;border:none;background-color:#227C9D !important;padding:15px 25px;border-radius:20px;font-weight:600;font-size:0.8rem;margin-top:35px;transition:0.5s}.login-input{background:white;color:#227C9D;border:1px solid #227C9D;height:3rem;padding:0.5rem;font-size:16px;font-weight:100;letter-spacing:10px;border-radius:5px;font-weight:400;font-size:1rem;letter-spacing:normal}h5{text-align:center;margin-top:2px;font-size:0.7rem;background-color:transparent;color:#227C9D}h5 a{background-color:inherit;color:inherit}

.game{height:100vh;width:100vw;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;background-color:#FEF9EF}.header{background-color:#227C9D;display:flex;justify-content:space-between;min-height:10vh;position:fixed}.nav-link{min-height:20% !important;margin:20px 0px;font-size:2rem;text-align:start;border-bottom:1px solid #00afb9}.header-icon{color:#FEF9EF}.game-card{width:90%;height:60%;background-color:#17C3B2 !important}.game-card h2{margin-top:15px;color:#FEF9EF}.gender-button{border:none;background-color:#FFCB77;color:#227C9D;box-shadow:2px 2px 3px 0px #00798c;padding:15px 25px;border-radius:6px;font-weight:700;font-size:1rem;margin-bottom:15px;transition:0.5s;margin:20px}.deck{height:'80%';width:'100%';overflow:'hidden';display:flex;flex-direction:column;justify-content:center;align-items:center;position:relative}.card-buttons{width:60%;display:flex;justify-content:space-between;position:relative;bottom:6rem}.card-button:hover{transform:scale(1.2)}.card-button{background-color:#FFCB77 !important}.card-icon{font-size:2rem !important}.card-button:hover{transform:scale(1.1)}.card-button:focus{outline:none}

.profile-container{display:flex;height:90vh;justify-content:center;align-items:center;background-color:#FEF9EF}.profile{background-color:#17C3B2;height:80vh;width:90%;color:white;padding:20px;border-radius:20px}.profile-content{margin-bottom:30px}.profile-button{color:#227C9D;border:none;background-color:#FFCB77 !important}.profile-link{border:1px solid #227C9D;border-radius:10px;padding:30px;color:white;background-color:#227C9D;font-size:1.3rem}

.matches,.likes{padding-top:30px;min-height:90vh;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:#FEF9EF}.match{background-color:#227C9D;width:70%;height:10%;padding:10px;margin:10px;border-radius:10px;color:white;font-weight:600;font-size:1.7rem;transition:all 0.6s;text-align:center;transition:all 0.6s}.like{background-color:#227C9D;width:70%;height:10%;padding:10px 20px;margin:10px;border-radius:10px;color:white;font-weight:600;font-size:1.7rem;transition:all 0.6s;display:flex;justify-content:space-between;align-items:center}.like.delete{animation:shake-fade 1.5s linear}.match:hover{transform:scale(1.1)}.like:hover{transform:scale(1.1)}.girl{background-color:#FE6D73 !important}.boy{background-color:#17C3B2 !important}.final-count{border:1px solid #FE6D73;border-radius:25px;padding:15px;font-size:1.3rem;margin-top:20px;position:relative;bottom:6.4rem;color:#FE6D73;font-weight:600}.final-end{height:65vh;width:80%;bottom:580px;display:flex;text-align:center;font-size:2rem;background-color:white;padding-top:100px;opacity:1;animation:fadeIn 1.5s}@keyframes fadeIn{0%{opacity:0}20%{opacity:0.4}60%{opacity:0.8}100%{opacity:1}}@keyframes shake-fade{0%{transform:translateX(0);opacity:1}10%{transform:translateX(40px);opacity:0.8}20%{transform:translateX(0px);opacity:0.6}30%{transform:translateX(-40px);opacity:0.4}40%{transform:translateX(0px);opacity:0.2}70%{transform:translateX(120px);opacity:0}100%{transform:translateX(120px);opacity:0}}

.left{transform:translateX(-1000px) rotateZ(-80deg);transition:0.4s transform;animation:none}.right{transform:translateX(1000px) rotateZ(80deg);transition:0.4s transform;animation:none}.enter{animation:enter 0.1s}@keyframes enter{from{transform:scale(0.3);opacity:0}to{transform:none;opacity:1}}body{-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.name{color:white !important;text-align:center;font-family:'Pacifico', cursive;padding:20px 20px;display:flex;flex-direction:column;justify-content:space-around;align-items:center;font-size:3.6rem;height:60vh;width:70vw;margin:30px;background:#17c3b2;background:linear-gradient(108deg, #17c3b2 0%, #ffcb77 88%)}.namecard{display:flex;flex-direction:column;justify-content:center;align-items:center;height:90vh;background-color:white}

